<template>
  <div id="singer-detail">
    <div class="music-container">
      <!-- 歌手介绍 -->
      <h2>歌手介绍</h2>
      <div class="singer-header">
        <img v-lazy="picUrl" alt="">
        <div class="info">{{briefDesc}}</div>
      </div>
      <!-- 热门歌曲 -->
      <h2>
        热门歌曲
        <button class="btn">播放全部</button>
      </h2>
      <div class="song-header">
        <div class="id nav">序号</div>
        <div class="name nav">歌名</div>
        <div class="songer nav">歌手</div>
        <div class="time nav">时长</div>
        <div class="album nav">专辑</div>
      </div>
      <div class="song-container">
        <song-item
          v-for="(item, index) in songs"
          :key="index"
          :index="index"
          :songId="item.id"
          :alId="item.al.id"
          :arId="item.ar[0].id"
          :songName="item.name"
          :alName="item.al.name"
          :arName="item.ar[0].name"
          :formatdt="item.format_dt"
          :dt="item.dt"
          @play="play"
        />
      </div>
      <!-- MV -->
      <h2 v-if="show">MV</h2>
      <div class="mv-container">
        <mv-item
          class="item"
          v-for="(item, index) in mv"
          :key="index"
          :mvId="item.id"
          :cover="item.imgurl"
          :name="item.name"
          :artistName="item.artistName"
          :artists="item.artists"
          :playCount="item.playCount"
        />
      </div>
    </div>
     <!-- 音乐播放器 -->
    <transition name="prograss">
         <music-prograss  class="music-prograss" v-show="musicPrograss"></music-prograss>
    </transition>
  </div>
</template>
<script>
// 工具包
import {
  req_singerAlbum,
  req_singerMv,
  req_singerDesc,
} from "../network/singer/index.js";
import { req_search } from "../network/search/index.js";
import tools from "../tools/formatData.js";
import { req_checkMusic } from "../network/song/index.js";
// 组件包
import songItem from "../components/item/songItem";
import mvItem from "../components/item/mvItem";
import musicPrograss from "../components/musicPrograss";

export default {
  name: "SingerDetail",
  components: {
    songItem,
    mvItem,
    musicPrograss
  },
  data() {
    return {
      // 歌手 id
      id: this.$route.query.id,
      // 歌手名字
      name: this.$route.query.name,
      // 歌手图像
      picUrl: "",
      mv: [],
      // 歌手介绍
      briefDesc: "",
      // 热门歌曲
      songs: [],
      show: false,

      // 歌曲进度条
      musicPrograss: this.$store.state.musicPrograss,
    };
  },
  watch: {
    songs: function() {
      this.songs.forEach((e, index, arr) => {
        let value = tools._msTos(e.dt);
        arr[index].format_dt = value;
      });
    },
     mv: function() {
        this.mv.forEach((e, index, arr) => {
          let value = tools._tranNumber(e.playCount, 2);
          arr[index].playCount = value;
        });
     },
     // 监听 vuex 中 musicPrograss 的变化， 使其能够动态赋值给 data 数据中
    '$store.state.musicPrograss': function() {
       this.musicPrograss = this.$store.state.musicPrograss;
    }
  },
  created() {
    this.getSingerMv();
    this.getSingerDesc();
    this.getSingerAlbum();
    this.search();
  },
  mounted() {
    this.init();
  },
  methods: {
    // 初始化环境
    init() {
      window.addEventListener("scroll", this.menu);
    },
    // 获取歌手 MV
    getSingerMv() {
      req_singerMv(this, { id: this.id })
        .then(res => {
          this.mv = res.mvs.slice(0, 8);
        })
        .catch(error => {
          console.log("获取歌手 MV可能出现错误",error);
        });
    },
    // 歌曲歌手信息
    getSingerDesc() {
      req_singerDesc(this, { id: this.id })
        .then(res => {
          this.briefDesc = res.briefDesc;
        })
        .catch(error => {
          console.log("获取歌曲歌手信息",error);
        });
    },
    // 获取歌手歌曲
    getSingerAlbum() {
      req_singerAlbum(this, { id: this.id })
        .then(res => {
          this.songs = res.songs;
          this.show = true;
        })
        .catch(error => {
          console.log("获取歌手歌曲可能出现错误",error);
        });
    },
    // 获取歌手头像
    search() {
      req_search(this, {
        keywords: this.name,
        type: 100,
      }).then(res => {
        this.picUrl = res.result.artists[0].picUrl;
      }).catch(error => {
        console.log("获取歌手头像可能出现错误",error);
      });
    },
    // 播放全部
    playAll() {
      let trackIds = this.songs.map(e => {
          return {id: e.al.id};
      })
      // 存入本地缓存
      window.localStorage.setItem("playList", JSON.stringify(trackIds));
      window.localStorage.setItem("playListCover", this.picUrl);
      window.localStorage.setItem("trackCount", 50);
      this.$router.push({
        path: `/song/${new Date().getTime()}`
      })
    },
    // 播放
    play(id, songName, dt) {
      //   判断歌曲是否可以播放
      req_checkMusic(this, {id}).then(res => {
        let result = res.success
        if(result) {
           let currentPlay = {
             playSongId: id,
             playSongname: songName,
             songTime: dt,
           }
           //  存入缓存
           window.localStorage.setItem("currentPlay", JSON.stringify(currentPlay));
          //  重新加载音乐
          this.$store.dispatch("saveMusicLoading", Math.random());
          //  触发音乐滚动条
          this.$store.dispatch("saveMusicPrograss", true);
        }
      }).catch(() => {
          this.$message({
            showClose: true,
            message: '亲爱的,暂无版权',
            type: 'error'
           });
          //  关闭掉加载
          this.$store.dispatch("saveLoadingFlag", false);
      }) 
    },
    menu() {
       let value = document.documentElement.scrollTop || document.body.scrollTop;
       if(value > 1200) {
            this.$store.dispatch("saveMusicPrograss", false);
       }
       if(value < 300) {
          this.$store.dispatch("saveMusicPrograss", true);
       }
    }
  },
  beforeDestroy() {
     window.removeEventListener("scroll", this.menu);
  }
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
#singer-detail {
  .music-container {
    h2 {
      text-align: center;
      margin-top: 20px;
      margin-bottom: 30px; 
      position: relative;
      .btn {
        position: absolute;
        right: 0;
        background-color: $colorA;
        color: colorD;
        padding: 10px 12px;
        text-align: center;
        border: none;
      }
    }
    .singer-header {
      height: 300px;
      margin-top: -30px;
      @include flex(flex-start);
      img {
        width: 200px;
        height: 200px;
        border-radius: 50%;
      }
      .info {
        widows: 600px;
        margin-left: 30px;
        height: 200px;
        padding-top: 30px;
        box-sizing: border-box;
        @include font(16px, 30px, $colorC);
        @include fontDHidden(6);
      }
    }
    .song-header {
      height: 30px;
      @include flex();
      border: 1px solid $colorE;
      background-color: $colorE;
      .nav {
        @include font(18px, 30px, $colorC);
        text-align: center;
        border-right: 1px solid $colorD;
        &:last-child {
          border: none;
        }
        &:first-child {
          width: 40px;
        }
        &:nth-child(n + 2) {
          flex: 1;
        }
      }
    }
    .song-container {
      margin-bottom: 70px;
      height: 2000px;
    }
    .mv-container {
      @include flex();
      flex-wrap: wrap;
    }
  }
   .music-prograss {
    position: fixed;
    bottom: -100px;
    left: 0;
    right: 0;
    margin-right: auto;
    margin-left: auto;
  } 
  .prograss-enter {
      bottom:-172px;
  }
  .prograss-enter-to {
    bottom: -100px;
  }
  .prograss-leave-to {
     bottom:-172px;
  }
  .prograss-enter-active , 
  .prograss-leave-active {
      transition: bottom .5s;
  }
}
</style>