<template>
  <div id="music-prograss">
    <div class="left">
      <div :class="['item','iconfont',play? 'icon-zanting': 'icon-bofang']" @click="isplay"></div>
    </div>
    <div class="song">
      <div class="name">{{playSongname}}</div> 
      <img v-lazy="songUrl" alt="">
    </div>
    <div class="right">
       <div class="prograss-container">
          <div class="current-time"></div>
          <div class="bar" @mousedown="barDown"></div>
       </div>
    <div class="time">{{Math.floor(songTime / 60000)+":"+Math.ceil(songTime % 60000 / 1000)}}</div>
    </div>

    <audio :src="audioUrl"></audio>

  </div>
</template>
<script>
import { req_song_url } from "../network/song/index.js";
import { req_search } from "../network/search/index.js";
export default {
  name: "prograss",
  data() {
    return {
      // 进度跳是否按下
      down: false,

      play: false,
      // 音频 url
      audioUrl: "",

      // 初始进度条长度
      prograssDistance: 0,
      // 当前进度条滑动的距离
      currentDistance: 0,

      // 当前歌曲时间
      timeStamp: 0,

      // 音频标签
      audio: this.$store.state.audio,
     
      
      // 单曲图片
      songUrl: "",

      // 实际滚动距离
      x: 0,
      
      // 从缓存中取数据
      playSongId: 0,
      playSongname: "",
      songTime: 0,

      
    };
  },
  watch: {
    '$store.state.play': function() {
      let audio = document.querySelector("audio");
      if (this.$store.state.play) {
        audio.play();
        this.play = true;
      } else {
        audio.pause();
        this.play = false;
      }
    },
    timeStamp: function() {
      // 更新进度条
      let x = this.timeStamp * (this.prograssDistance / this.songTime);
      document.querySelector(".bar").style.left = x + "px";
      document.querySelector(".current-time").style.width = x + "px";
      // 保存实际滚动距离
      this.x = x;
    },
    '$store.state.musicLoading': function() {
         this.prograssInit();
         this.getStorage();
         this.getsongUrl();
         this.getSongImg();
    }
  },
  mounted() {
    //  缓存是否存在歌曲
     if(window.localStorage.getItem("currentPlay")) {
        this.getStorage();
        this.prograssInit();
        this.getsongUrl();
        this.getSongImg(); 
        this.$store.dispatch("saveMusicPrograss", true);
     }
  },
  methods: {
    // 从缓存中取数据
    getStorage() {
        let data = JSON.parse(window.localStorage.getItem("currentPlay"));
        this.playSongId = data.playSongId;
        this.playSongname = data.playSongname;
        this.songTime = data.songTime;
    },
    // 根据距离换算当前歌曲时间
    currentTime() {
      let audio = document.querySelector("audio");
      let x = this.currentDistance * (this.songTime / this.prograssDistance) / 1000;
      audio.currentTime = x;
      // 打开音频播放
      this.$store.dispatch("savePlay", true);
    },
    // 滚动条监听
    prograssInit() {
      // 保存进度条长度
      this.prograssDistance =
        document.querySelector(".prograss-container").offsetWidth -
        document.querySelector(".bar").offsetWidth;

      // 监听进度条拖拽
      document.onmousemove = e => {
        if (this.down) {
          let x = e.pageX;
          let bar = document.querySelector(".bar");
          // 进度条容器
          let container = document.querySelector(".prograss-container");
          let current = document.querySelector(".current-time");
          let prograss_left = document.querySelector("#music-prograss")
            .offsetLeft;
          let container_left = container.offsetLeft;
          let container_width = container.offsetWidth;
          let bar_width = bar.offsetWidth;
          // 移动的距离
          let distance = x - container_left - prograss_left - bar_width / 2;

          // 判断是否超出范围
          distance = distance < 0 ? 0 : distance;
          distance =
            distance > container_width - bar_width
              ? container_width - bar_width
              : distance;

          // 设置样式
          bar.style.left = distance + "px";
          current.style.width = distance + "px";

        
          // 保存当前滑动的距离
          this.currentDistance = distance;
        }
      };
      // 停止拖拽
      document.onmouseup = () => {
        if (this.down) {
          // 停止拖拽
          this.down = false;
          // 更新当前音频时间
          this.currentTime();
        }
      };

     
    },
    // 音频监听
    audioInit() {
      // 设置播放（不生效的）
      // this.$store.dispatch("savePlay", true);
      let audio = document.querySelector("audio");
      // 播放中
      audio.addEventListener("timeupdate", ()=> {
        // 获取此时播放时间
        let currentTime = audio.currentTime * 1000;
        // 更新时间戳
        this.timeStamp = currentTime;
      });

      // 播放结束
      audio.addEventListener("ended", () => {
        this.$store.dispatch("saveMusicPrograss", false);
        console.log("音频播放结束");
         // 进度条到底
        this.timeStamp = this.songTime;

        // 清空
        this.songId = "";
        this.songName = "";
        this.songTime = "";
      })
    },
    // 拖拽
    barDown() {
      // 允许拖拽
      this.down = true;
      // 音频暂停
      this.$store.dispatch("savePlay", false);
    },
    // 获取音频
    getsongUrl() {
      // 音频暂停
      this.$store.dispatch("savePlay", false);
      // 回正时间戳
      this.timeStamp = 0;
      req_song_url(this, { id: this.playSongId })
        .then(res => {
          this.audioUrl = res.data[0].url;
          // 初始化 audio
          this.audioInit();
        })
        .catch(error => {
          console.log("获取音频可能出现错误",error);
        });
    },
    // 获取歌曲图片
    getSongImg() {
      req_search(this, {
        keywords:this.playSongname
        }).then(res => {
            this.songUrl = res.result.songs[0].al.picUrl;
        }).catch(error => {
          console.log("获取歌曲图片可能出现错误",error);
        })
    },
    isplay() {
      // 获取当前状态
      let flag = this.$store.state.play;
      this.$store.dispatch("savePlay", !flag);
    },
  },
  beforeDestroy() {
    this.$store.dispatch("savePlay", false);
  },
};
</script>
<style lang="scss">
@import "../assets/scss/icons.scss";
@import "../assets/scss/music.scss";
#music-prograss {
  user-select: none;
  width: 100%;
  height: 40px;
  background-image: linear-gradient(120deg, #d4fc79 0%, #96e6a1 100%);
  margin: 100px auto;
  display: flex;
  justify-content: flex-start;
  padding: 10px 20px;
  .left {
    display: flex;
    justify-content: flex-start;
    margin-right: 20px;
    .item {
      width: 100px;
      height: 40px;
      margin-right: 10px;
      background-repeat: no-repeat;
      background-size: 40px 40px;
      background-position: 10px 10px;
      margin-left: 260px;
      padding: 6px;
      &:hover {
        color: $colorA;
      }
    }
  }
  .song {
    width: 230px;
    height: 40px;
    margin-right: 30px;
    @include flex();
    .name {
       height: 40px;
       @include font(18px, 40px, $colorD);
       margin-right: 6px;
       @include fontHidden();
    }
    img {
      display: block;
      height: 100%;
    }
  }
  .right {
    display: flex;
    align-items: center;
    .prograss-container {
      position: relative;
      width: 600px;
      height: 6px;
      background-color: $colorD;
      border-radius: 40px;
      .current-time {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 0px;
        height: 100%;
        background-color: $colorA;
      }
      .bar {
        position: absolute;
        top: -5px;
        left: 0px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background-color: $colorA;
      }
    }
    .time {
      color: $colorD;
      margin-left: 12px;
    }
  }
}
</style>
