<template>
  <div id="song">
    <div class="id item-d">{{index + 1}}</div>
    <div class="name item-d hover" @click="goSong(songId, songName, dt)">{{songName}}</div>
    <div class="songer item-d hover" @click="goSingerDetail(arId, arName)">{{arName}}</div>
    <div class="time item-d">{{formatdt}}</div>
    <div class="album item-d hover" @click="album(alId)">{{alName}}</div>
  </div>
</template>
<script>
export default {
  name: "song",
  props: {
    // 下标
    index: Number,

    // 歌曲 id
    songId: Number,
    // 专辑 id
    alId: Number,
    // 歌手 id
    arId: Number,

    // 歌曲名字
    songName: String,
    // 专辑名字
    alName: String,
    // 歌手名字
    arName: String,

    // 可视时长
    formatdt: String,

    // 真实时长
    dt: Number,
  },
  methods: {
    // 跳转到播放页
    goSong(id, songName, dt) {
      this.$emit("play", id, songName, dt);
    },
    goSingerDetail(id, name) {
      this.$router.push({
        path: `/singerDetail/${new Date().getTime()}`,
        query: {
          id,
          name,
        },
      });
    },
    album(id) {
      this.$message({
        showClose: true,
        message: "链接：https://music.163.com/#/album?id=" + id,
        type: "success",
      });
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
#song {
  height: 40px;
  padding-left: 12px;
  box-sizing: border-box;
  @include flex();
  @include font(14px, 30px, $colorB);
  &:nth-child(even) {
    background-color: $colorH;
  }
  &:nth-child(odd) {
    background-color: $colorD;
  }
  .item-d {
    cursor: pointer;
    text-align: center;
    @include font(14px, 30px, $colorB);
    @include fontHidden();
    &:first-child {
      width: 40px;
    }
    &:nth-child(n + 2) {
      flex: 1;
    }
  }
  .hover {
    &:hover {
      text-decoration: underline;
      color: $colorA;
      cursor: pointer;
    }
  }
}
</style>