import { require } from "../require";
import { singerList, singerMv, singerDesc, singerAlbum} from "../url";

/**
 * 获取歌手列表 singer
 */
function req_singer(context, params) {
    return require(singerList, params, context);
}
/**
 * 获取歌手 MV
 */
function req_singerMv(context, params) {
    return require(singerMv, params, context);
}

/**
 * 获取歌手介绍
 */
function req_singerDesc(context, params) {
    return require(singerDesc, params, context);
}

/**
 * 获取歌手专辑
 */
function req_singerAlbum(context, params) {
    return require(singerAlbum, params, context);
}
export {
    req_singer,
    req_singerAlbum,
    req_singerMv,
    req_singerDesc
}